<template>
  <!-- eslint-disable vue/valid-v-for -->
  <li
    class="dvs-my-4 dvs-collapsable"
    :class="{'dvs-open': sliceOpen}"
  >
    <strong class="dvs-block dvs-mb-2 dvs-switch-sm dvs-text-sm dvs-flex dvs-justify-between dvs-items-center dvs-w-full">
      <div
        class="dvs-flex dvs-items-center dvs-justify-between dvs-w-full"
        :style="{color: theme.panel.color}"
      >
        <div class="dvs-flex dvs-w-full">
          <div class="handle dvs-mr-2 dvs-cursor-move">
            <menu-icon
              style="margin-top:2px;"
              :style="theme.panelIcons"
            />
          </div>
          <div
            dusk="slice-label"
            class="dvs-relative dvs-w-full dvs-cursor-pointer"
            :class="{'dvs-opacity-75': !sliceHasFieldsOrSlices}"
            @click="toggleSlice()"
            @mouseenter="markSlice(true, slice)"
            @mouseleave="markSlice(false, slice)"
            v-html="editorLabel"
          ></div>
        </div>
        <div
          class="dvs-ml-2 dvs-relative dvs-p-2 dvs-rounded-sm dvs-flex dvs-items-center"
          @mouseenter="moreHovered = true"
          @mouseleave="moreHovered = false"
          :style="{backgroundColor: this.theme.panelCard.background}"
        >
          <more-icon
            w="10"
            h="10"
            :style="{color: 'white'}"
          />

          <div
            class="dvs-overflow-hidden dvs-absolute dvs-z-10 dvs-pin-t dvs-pin-r dvs-rounded-sm"
            style="width:175px;"
            :style="{backgroundColor: this.theme.panelCard.background}"
            v-if="moreHovered"
          >
            <div class="dvs-pt-2 dvs-flex dvs-items-end dvs-flex-wrap">
              <div class="dvs-w-1/2">
                <div
                  class="dvs-mr-2 dvs-ml-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :class="{'dvs-cursor-pointer': hasChildSlot, 'dvs-cursor-not-allowed dvs-opacity-50': !hasChildSlot}"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="requestInsertSlice()"
                >
                  <add-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Insert Slice</div>
                </div>
              </div>

              <div class="dvs-w-1/2">
                <div
                  class="dvs-cursor-pointer dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="jumpToSlice()"
                >
                  <locate-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Find</div>
                </div>
              </div>

              <div class="dvs-w-1/2">
                <div
                  class="dvs-cursor-pointer dvs-ml-2 dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="copySlice(slice, false)"
                >
                  <copy-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Duplicate</div>
                </div>
              </div>

              <div class="dvs-w-1/2">
                <div
                  class="dvs-cursor-pointer dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="sliceSettings()"
                >
                  <cog-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Settings</div>
                </div>
              </div>

              <div class="dvs-w-1/2">
                <div
                  class="dvs-cursor-pointer dvs-ml-2 dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="requestEditSlice()"
                >
                  <create-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Edit</div>
                </div>
              </div>

              <div class="dvs-w-1/2">
                <div
                  class="dvs-cursor-pointer dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="removeSlice()"
                >
                  <remove-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Remove</div>
                </div>
              </div>

              <div
                class="dvs-w-1/2"
                v-if="!child"
              >
                <div
                  class="dvs-cursor-pointer dvs-ml-2 dvs-mr-2 dvs-items-center dvs-flex dvs-flex-col dvs-mb-2 dvs-border dvs-rounded-sm dvs-p-2"
                  :style="{ borderColor: theme.panelIcons.color }"
                  @click="requestCopyToAnotherPage()"
                >
                  <copy-icon
                    w="25"
                    h="25"
                    :style="theme.panelIcons"
                  />
                  <div class="dvs-text-xs dvs-text-center dvs-leading-none dvs-pt-2">Copy</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </strong>

    <portal to="devise-root">
      <copy-slice-to-page
        :slice="slice"
        @close="showCopyToAnotherPage = false"
        v-if="showCopyToAnotherPage"
      ></copy-slice-to-page>
    </portal>

    <manage-slice
      ref="manageslice"
      v-if="manageSlice === true"
      @cancel="manageSlice = false"
      @addSlice="addSlice"
      @editSlice="editSlice"
      @removeSlice="removeSlice"
      :slice="slice"
      :mode="manageSliceMode"
    />

    <div
      class="dvs-collapsed dvs-mb-6"
      v-if="sliceOpen"
    >
      <fieldset
        v-for="(field, key) in sliceConfig(slice).fields"
        class="dvs-fieldset dvs-mb-1"
        :key="key"
      >
        <div v-if="theFields[key]">
          <color-editor
            dusk="color-editor-link"
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'color'"
          ></color-editor>

          <checkbox-editor
            dusk="checkbox-editor-link"
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'checkbox'"
          ></checkbox-editor>

          <datetime-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'datetime'"
          ></datetime-editor>

          <image-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'image'"
          ></image-editor>

          <file-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'file'"
          ></file-editor>

          <link-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'link'"
          ></link-editor>

          <number-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'number'"
          ></number-editor>

          <select-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'select'"
          ></select-editor>

          <textarea-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'textarea'"
          ></textarea-editor>

          <text-editor
            dusk="text-editor-link"
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            v-if="field.type === 'text'"
          ></text-editor>

          <wysiwyg-editor
            @change="editField($event, field, key)"
            v-model="theFields[key]"
            :options="field"
            :namekey="key"
            :show="slice.metadata.show"
            v-if="field.type === 'wysiwyg'"
          ></wysiwyg-editor>
        </div>
      </fieldset>
    </div>

    <div class="dvs-collapsed">
      <help
        v-if="slice.metadata.type === 'model'"
        :style="theme.panel"
      >Be aware that these entries are model entries. That means they are managed in your database by another tool or by an admin section in your adminitration.</help>
    </div>

    <div
      class="dvs-collapsed dvs-ml-4"
      v-if="hasChildSlot"
      :style="{
          background: this.theme.panelCard.background,
          padding: '2px',
          margin: '-4px 0 0 -4px',
          minHeight: '15px'
        }"
    >
      <draggable
        v-model="slice.slices"
        element="ul"
        class="dvs-list-reset dvs-rounded-sm"
        v-if="slice.metadata.type !== 'model'"
        :style="{
          padding: '2px',
          margin: '-4px 0 0 1em',
          minHeight: '15px'
        }"
        :options="{
          handle: '.handle', 
          group: {name: 'g1'},
          animation:200,
          ghostClass: 'dvs-ghost',
        }"
      >
        <template v-for="s in sliceSlices">
          <slice-editor
            :key="randomString(8)"
            :devise="s"
            :child="true"
            @addSlice="addSlice"
            @editSlice="editSlice"
            @removeSlice="removeSlice"
            @copySlice="copySlice"
          />
        </template>
      </draggable>
    </div>
  </li>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import Strings from '../../../mixins/Strings';

export default {
  name: 'SliceEditor',
  data () {
    return {
      showDropZone: false,
      manageSlice: false,
      manageSliceMode: 'inserting',
      pageSlices: [],
      moreHovered: false,
      sliceOpen: false,
      showCopyToAnotherPage: false,
    };
  },
  mounted () {
    if (this.slice.slices) {
      this.pageSlices = this.slice.slices;
    }
  },
  methods: {
    toggleSlice () {
      this.sliceOpen = !this.sliceOpen;
    },
    toggleSliceTools () {
      this.slice.metadata.tools = !this.slice.metadata.tools;
    },

    // Marking Slice
    markSlice (on) {
      window.window.deviseSettings.$bus.$emit('markSlice', this.devise, on);
    },
    jumpToSlice () {
      window.window.deviseSettings.$bus.$emit('jumpToSlice', this.slice);
    },
    sliceSettings () {
      window.window.deviseSettings.$bus.$emit('openSliceSettings', this.slice);
    },

    // Adding, Editing and Removing Slices
    requestInsertSlice () {
      if (this.hasChildSlot) {
        this.manageSliceMode = 'inserting';
        this.manageSlice = true;
      }
    },
    addSlice (slice, referringSlice) {
      if (typeof referringSlice === 'undefined') {
        referringSlice = this.slice;
      }
      this.$emit('addSlice', slice, referringSlice);
      this.manageSlice = false;
    },
    requestEditSlice () {
      this.manageSliceMode = 'editing';
      this.manageSlice = true;
      this.$nextTick(() => {
        if (this.$refs.manageslice) {
          this.$refs.manageslice.action = 'edit';
        }
      });
    },
    editSlice (slice, referringSlice) {
      if (typeof referringSlice === 'undefined') {
        referringSlice = this.slice;
      }
      this.$emit('editSlice', slice, referringSlice);
      this.manageSlice = false;
    },
    copySlice (slice, referringSlice) {
      if (referringSlice === null) {
        referringSlice = Object.assign({}, this.slice);
      }

      if (referringSlice === false) {
        referringSlice = null;
      }

      this.$emit('copySlice', slice, referringSlice);
    },
    removeSlice (slice, referringSlice) {
      if (typeof slice === 'undefined') {
        slice = this.slice; // eslint-disable-line
      } else if (typeof referringSlice === 'undefined') {
        referringSlice = this.slice;
      }
      this.$emit('removeSlice', slice, referringSlice);
      this.manageSlice = false;
    },
    requestCopyToAnotherPage () {
      this.showCopyToAnotherPage = true
    },
    editField (value, field, key) {
      // Update the slice field
      this.slice[key] = Object.assign({}, value);

      // Send out the notification
      window.deviseSettings.$bus.$emit('devise-field-edited', { field, value });
    },
  },
  computed: {
    ...mapGetters('devise', ['component', 'fieldConfig', 'sliceConfig']),
    ...mapState('devise', ['devMode']),
    slice () {
      return this.devise;
    },
    sliceSlices () {
      return (this.slice.slices) ? this.slice.slices : []
    },
    theFields () {
      const fields = {};
      for (const potentialField in this.slice) {
        if (
          this.slice.hasOwnProperty(potentialField) &&
          potentialField !== 'slices' &&
          potentialField !== 'metadata' &&
          potentialField !== 'settings' &&
          typeof this.slice[potentialField] === 'object'
        ) {
          fields[potentialField] = this.slice[potentialField];
          if (typeof fields[potentialField].enabled === 'undefined') {
            this.$set(fields[potentialField], 'enabled', true);
          }
        }
      }
      return fields;
    },
    sliceHasFieldsOrSlices () {
      let count = Object.keys(this.theFields).length;
      if (this.slice.slices) {
        count += this.slice.slices.length;
      }

      return count > 0;
    },
    hasChildSlot () {
      const component = this.component(this.slice.metadata.name);

      if (component.has_child_slot) {
        return true;
      }

      return false;
    },
    editorLabel () {
      const acceptedFieldTypes = {
        text: 'text',
        number: 'text',
        datetime: 'text',
        image: 'url',
        link: 'text',
        select: 'value',
      };

      let devMode = '';
      if (this.devMode) {
        devMode = `<div class="dvs-text-sm dvs-uppercase dvs-opacity-75">Instance Id: ${
          this.slice.metadata.instance_id
          }</div>`;
      }

      for (const field in this.theFields) {
        if (this.theFields.hasOwnProperty(field)) {
          const f = this.theFields[field];

          if (f.editorLabel && f[acceptedFieldTypes[f.type]]) {
            let label = f[acceptedFieldTypes[f.type]];

            if (f.type === 'image') {
              label = `<div class="dvs-rounded dvs-bg-cover" style="background-image: url('${label}'); height:100px; width:200px;"></div>`;
            } else {
              label = `${label.toLowerCase()}`;
            }

            if (label) {
              return `${devMode}<div class="dvs-capitalize">${label}</div><div class="dvs-absolute dvs-pin-t dvs--mt-4 dvs-text-xs dvs-opacity-25 dvs-uppercase">${
                this.slice.metadata.label
                }</div>`;
            }
          }
        }
      }
      return `${devMode}<div class="dvs-capitalize">${
        this.slice.metadata.label
        }</div>`;
    },
  },
  props: {
    child: {
      default: false,
    },
  },
  mixins: [Strings],
  components: {
    AddIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/PlusCircleIcon'),
    CheckboxEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Checkbox'),
    CogIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/SettingsIcon'),
    CopyIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/CopyIcon'),
    ColorEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Color'),
    DatetimeEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Datetime'),
    CreateIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/Edit3Icon'),
    draggable: () => import(/* webpackChunkName: "devise-editors" */ 'vuedraggable'),
    FileEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/File'),
    ImageEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Image'),
    LinkEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Link'),
    LocateIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/TargetIcon'),
    ManageSlice: () => import(/* webpackChunkName: "devise-editors" */ './ManageSlice'),
    MenuIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/MenuIcon'),
    MoreIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/MoreVerticalIcon'),
    NumberEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Number'),
    RemoveIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/TrashIcon'),
    SelectEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Select'),
    SliceEditor: () => import(/* webpackChunkName: "devise-editors" */ './SliceEditor'),
    CopySliceToPage: () => import(/* webpackChunkName: "devise-editors" */ './CopySliceToPage'),
    TextareaEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Textarea'),
    TextEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Text'),
    WysiwygEditor: () => import(/* webpackChunkName: "devise-editors" */ '../editor/Wysiwyg'),
  },
};
</script>
